.button {
  padding: 10px 0 10px 0;

  text-align: center;
  font-family: $oswald;
  color: $font-color-white;
  vertical-align: middle;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;

  background-color: $color-button-green;
  border: none;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: #5eaa2f;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
